.cardBox {
  background-color: aliceblue;
  border: 1px solid black;
  border-radius: 5px;
  margin: 10px;
  min-height: 250px;
  padding: 10px;
  width: 150px;
  flex-basis: 20%;
}
.cardBox h1 {
  color: green;
  font-size: 28px;
  font-weight: bold;
}
.cardBox h2 {
  font-size: 16px;
  font-weight: normal;
  word-break: break-all;
}
