.App {
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
h1 {
  font-family: Bigelow Rules;
  font-size: 72px;
  color: #0ccac4;
}
.App p.title {
  font-size: 36px;
  font-weight: bold;
}
.App-link {
  color: #61dafb;
}
input {
  margin-bottom: 1rem;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
